import React, { Component } from 'react'
import { ExperienceList } from '../components/ResumeList.js';

class ExperienceItem extends Component {
    constructor(props) {
        super(props);
        this.state = { experienceUpdate: { bullet: [] }, edittable: false };
    }

    componentDidMount() {
        const experienceUpdate = this.props.experience
        this.setState({ experienceUpdate });
    }

    handleEdit = e => {
        this.setState({ edittable: true });
    }

    handleChange = e => {
        const experienceUpdate = this.state.experienceUpdate;;
        experienceUpdate[e.target.name] = e.target.value;
        this.setState({ experienceUpdate });
    }

    handleBulletChange = (index, e) => {
        const experienceUpdate = this.state.experienceUpdate;;
        experienceUpdate.bullet[index] = e.target.value;
        this.setState({ experienceUpdate });
    }

    addBullet = e => {
        const experienceUpdate = this.state.experienceUpdate;;
        const bullet = experienceUpdate.bullet
        experienceUpdate.bullet = [...bullet, ''];
        this.setState({ experienceUpdate });
    }

    deleteBullet = (index, e) => {
        const experienceUpdate = this.state.experienceUpdate;
        const bullet = experienceUpdate.bullet
        bullet.splice(index, 1);
        this.setState({ experienceUpdate });
    }

    handleUpdate = e => {
        e.preventDefault();
        this.setState({ edittable: false });
    }

    render() {
        const { experience, deleteexperience, userID, updateexperience } = this.props;
        const { experienceUpdate, edittable } = this.state;
        const experienceList =
            <div className='card card-body text-center'>
                <div>
                    {
                        edittable ? null :
                            <button className='btn btn-outline-warning fa fa-edit float-left border-0 mb-3' onClick={this.handleEdit}></button>
                    }
                    <button className='fa fa-trash float-right btn btn-outline-danger mb-3 border-0' type='submit' onClick={deleteexperience}></button>
                </div>
                <div className="form-group mb-3">
                    <form onSubmit={this.handleUpdate}>
                        {ExperienceList.map((list, index) => (
                            <div key={`experienceupdateli+${experience}+${index}`}>
                                {edittable ?
                                    <div className="input-group mb-3" key={`expdiv1${index}`}>
                                        <span className="input-group-text" id="inputGroup-sizing-default" key={`experiencespaninput+${list}+${experience}+${index}`}>{list.toUpperCase()}</span>
                                        <input className='form-control' required='required' placeholder='required' key={`experienceinputedit+${list}+${experience}+${index}`} name={list} onChange={this.handleChange} value={experienceUpdate[list]} />
                                    </div>
                                    :
                                    <div className='input-group mb-3' key={`expdiv2${index}`}>
                                        <span className="input-group-text" id="inputGroup-sizing-default" key={`experiencespanli+${list}+${experience}+${index}`}>{list.toUpperCase()}</span>
                                        <div className='form-control' key={`${index}div3`}>{experience[list]}</div>
                                    </div>
                                }
                            </div>
                        ))
                        }
                        <div className='form-group mb-3'>
                            <div className="input-group-prepend">
                                <span className="input-group-text mb-3" id="inputGroup-sizing-default">Bullets</span>
                                {edittable
                                    ? <button className=" fa fa-plus-circle btn btn-outline-primary border-0 mb-3" type='button' onClick={this.addBullet}></button>
                                    : null}
                            </div>
                            {
                                experienceUpdate.bullet.map(
                                    (bullet, index) =>
                                        (
                                            <div key={`bulletupdateli+${index}`}>
                                                {
                                                    edittable ?
                                                        (
                                                            <div className='input-group mb-3' key={`${index}div10d`}>
                                                                <div key={`bulletdiv0 +${index}`} className="input-group-prepend" >
                                                                    <span key={`bulletadd5+${index}span`} className="input-group-text" id="inputGroup-sizing-default">{index + 1}</span>
                                                                </div>
                                                                <input className="form-control" required='required' placeholder='required' key={`bulletinputedit+${index}`} onChange={this.handleBulletChange.bind(this, index)} value={experienceUpdate['bullet'][index]} />
                                                                <button className='fa fa-times float-right btn btn-outline-danger border-0' key={`deletebtn+${index}`} type='button' onClick={this.deleteBullet.bind(this, index)}></button>
                                                            </div>

                                                        )
                                                        : (
                                                            <div className='form-group' key={`${index}div10dd`}>
                                                                <span className="input-group-text" id="inputGroup-sizing-default" key={`bulletsapnli+${index}`}>{bullet}</span>
                                                            </div>)
                                                }
                                            </div>
                                        )
                                )
                            }

                        </div>
                        {!edittable
                            ? null
                            :
                            <button className='btn btn-outline-dark fa fa-pencil border-0 mb-3' type='submit' onClick={updateexperience.bind(this, userID, experience._id, experienceUpdate)} ></button>
                        }
                    </form>
                </div>
            </div>


        return (
            <div>
                {experienceList}
            </div >
        )
    }
}

export default ExperienceItem;




