import React, { Component } from 'react'
import { CourseworkList } from '../components/ResumeList.js';

class CourseworkItem extends Component {

    constructor(props) {
        super(props);
        this.state = { courseworkUpdate: {}, edittable: false };
    }

    componentDidMount() {
        const courseworkUpdate = this.props.coursework;
        this.setState({ courseworkUpdate });
    }

    handleEdit = e => {
        this.setState({ edittable: true });
    }

    handleChange = e => {
        const courseworkUpdate = this.state.courseworkUpdate
        courseworkUpdate[e.target.name] = e.target.value;
        this.setState({ courseworkUpdate })
    }

    handleUpdate = e => {
        e.preventDefault();
        this.setState({ edittable: false });
    }

    handleDelete = e => {
        e.preventDefault();
        this.setState({ edittable: false });
    }

    render() {
        const { userID, deletecoursework, updatecoursework, coursework } = this.props;
        const { courseworkUpdate, edittable } = this.state;
        const CourseworkItem =
            <div className='card card-body text-center' >
                <div>
                    {
                        edittable ? null :
                            <button className='btn btn-outline-warning fa fa-edit float-left border-0' onClick={this.handleEdit}>
                            </button>
                    }
                    <button className='fa fa-times float-right btn btn-outline-danger border-0' type='submit' onClick={deletecoursework}></button>
                </div>
                <div className='form-group'>
                    <form onSubmit={this.handleUpdate}>
                        {
                            CourseworkList.map(
                                (list, index) =>
                                    <div key={`courseworkupdatediv1${index}`}>
                                        {edittable ?
                                            <div className='input-group mb-3' key={`courseworkupdatediv2${index}`}>
                                                <div className="input-group-prepend" key={`${index}courseworkdiv1`}>
                                                    <span className="input-group-text" id="inputGroup-sizing-default" key={`courseworkspaninput${index}`}>{list.toUpperCase()}</span>
                                                </div>
                                                <input required='required' placeholder='required' className='form-control' key={`courseworkinputedit${index}`} name={list} onChange={this.handleChange} value={courseworkUpdate[list]} />
                                            </div>
                                            :
                                            <div key={`courseworkupdatediv3${index}`}>
                                                <div key={`courseworkspandiv4+${index}`}>{coursework[list]}</div>
                                            </div>

                                        }
                                    </div>
                            )
                        }
                        {!edittable
                            ? null
                            : <button className='btn btn-outline-dark border-0 fa fa-pencil' type='submit' onClick={updatecoursework.bind(this, userID, coursework._id, courseworkUpdate)}></button>
                        }
                    </form>
                </div>
            </div>

        return (
            <div>
                {CourseworkItem}
            </div>
        )
    }
}

export default CourseworkItem;



