import React, { Component } from 'react';
import { connect } from 'react-redux';
import { fetchmessage, updatemessage, addmessage,deletemessage} from '../store/actions/message.js';
import MessageItem from '../components/MessageItem.js'

class Message extends Component {
    constructor(props) {
        super(props)
    }

    componentDidMount() {
        this.props.fetchmessage(this.props.currentUser.id);
    }
    
    render() {
        const {messages,currentUser,deletemessage,updatemessage,addmessage} = this.props;
        const messageList = 
        messages.map((message)=>(
            <MessageItem 
            key = {`message${message._id}`}
            message = {message}
            currentUser = {currentUser}
            deletemessage = {deletemessage}
            updatemessage = {updatemessage}
            addmessage = {addmessage}
            />
        )

        )
        return (
            <div className = 'container'>
            {messageList}
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        messages: state.message,
        currentUser: state.currentUser.user
    }
}

Message.defaultProps = {
    message:[]
}

export default connect(mapStateToProps,{fetchmessage,addmessage,deletemessage,updatemessage})(Message);


