import React, { Component } from 'react';
import { connect } from 'react-redux';
import { SchoolList } from '../components/ResumeList.js';
import { addschool } from '../store/actions/school.js';

class SchoolAddForm extends Component {
    constructor(props) {
        super(props);
        this.state = { school: {}, showForm: false };
    }
    handleAdd = () => {
        const showForm = !this.state.showForm
        this.setState({ showForm });

    };

    handleChange = e => {
        const school =  this.state.school;
        school[e.target.name] = e.target.value;
        this.setState({ school })
    }

    handleSubmit = e => {
        e.preventDefault();
        this.setState({ school: {}, showForm: false });
    }

    render() {
        const { school, showForm } = this.state;
        const { addschool, userID } = this.props;
        const schoolAddList = (
            SchoolList.map((list, index) =>
                (<div key={`schooladdli+${list}+${index}`} className='input-group mb-3'>
                    <div key={`schoolAdddiv1+${list}+${index}`} className="input-group-prepend">
                        <span key={`schoolAddspan1+${list}+${index}`} className="input-group-text" id="inputGroup-sizing-default">{list.toUpperCase()}</span>
                    </div>
                    <input className='form-control' placeholder ='required' required= 'required' id="inputGroup-sizing-default" key={`schooleditinput+${list}+${index}`} name={list} onChange={this.handleChange} value={school[list]} />
                </div>)
            )
        )

        return (
            <div className='container' style={{ marginTop: 100, marginBottom: 50, letterSpacing: 2 }}>
                <button className='fa fa-align-justify btn btn-secondary' style={{ marginBottom: 25 }} onClick={this.handleAdd}>Add Schools</button>
                {
                    showForm
                        ?
                        (
                            <form className='form-grounp' onSubmit={this.handleSubmit}>
                                {schoolAddList}
                                <button type='submit' className="btn btn-outline-primary fa fa-plus border-0" onClick={addschool.bind(this, userID, school)}></button>
                            </form>
                        )
                        : null
                }

            </div>
        )
    }
}



export default SchoolAddForm


