import React, { Component } from 'react'
import { ProfileList } from '../components/ResumeList.js';
import profileImg from '../images/profileImg.png';

class ProfileItem extends Component {

    constructor(props) {
        super(props);
        this.state = { profileUpdate: {}, edittable: false };
    }

    componentDidMount() {
        const profileUpdate = this.props.profile;
        this.setState({ profileUpdate });
    }

    handleEdit = e => {
        this.setState({ edittable: true });
    }

    handleChange = e => {
        const profileUpdate = this.state.profileUpdate
        profileUpdate[e.target.name] = e.target.value;
        this.setState({ profileUpdate })
    }

    handleUpdate = e => {
        e.preventDefault();
        this.setState({ edittable: false });
    }

    render() {
        const { userID, updateprofile, profile } = this.props;
        const { profileUpdate, edittable } = this.state;
        const ProfileItem =
            <div className='card card-body text-center' >
                {
                    edittable ? null :
                        <button className='btn btn-outline-warning fa fa-edit float-left border-0' onClick={this.handleEdit}>
                        </button>
                }
                <img className="mb-3 mt-3 mx-auto card-img-top rounded text-center w-25 h-25" src={profile.imageUrl ? profile.imageUrl : profileImg} alt="Profile Image" />
                <div className='form-group'>
                    <form onSubmit={this.handleUpdate}>
                        {
                            ProfileList.map(
                                (list, index) =>
                                    <div key={`profileupdatediv1${index}`}>
                                        {edittable ?
                                            <div className='input-group mb-3' key={`profileupdatediv2${index}`}>
                                                <div className="input-group-prepend" key={`${index}profilediv1`}>
                                                    <span className="input-group-text" id="inputGroup-sizing-default" key={`profilespaninput${index}`}>{list.toUpperCase()}</span>
                                                </div>
                                                <textarea className='form-control' required='required' placeholder='required' key={`profileinputedit${index}`} name={list} onChange={this.handleChange} value={profileUpdate[list]} />
                                            </div>
                                            :
                                            <div className='input-group mb-3' key={`profileupdatediv3${index}`}>
                                                <span className="input-group-text" id="inputGroup-sizing-default" key={`profilespanli${index}`}>{list.toUpperCase()}</span>
                                                <div className='form-control' key={`${index}div4`}>{profile[list]}</div>
                                            </div>
                                        }
                                    </div>
                            )
                        }
                        {!edittable
                            ? null
                            : <button className='btn btn-outline-dark border-0 fa fa-pencil' type='submit' onClick={updateprofile.bind(this, userID, profile._id, profileUpdate)}></button>
                        }
                    </form>
                </div>
            </div>

        return (
            <div>
                {ProfileItem}
            </div>
        )
    }
}

export default ProfileItem;


