import React, { Component } from 'react';
import { connect } from 'react-redux';
import { ExperienceList } from '../components/ResumeList.js';
import { addexperience } from '../store/actions/experience.js';

class ExperienceAddForm extends Component {
    constructor(props) {
        super(props);
        this.state = { experience: { bullet: [] }, showForm: false, };
    }

    handleAdd = () => {
        const showForm = !this.state.showForm
        this.setState({ showForm });
    };

    handleBulletAdd = () => {
        const experience = this.state.experience
        const bullet = experience.bullet;
        experience.bullet = [...bullet, ''];
        this.setState({ experience });
    }

    handleChange = e => {
        const experience = this.state.experience;
        experience[e.target.name] = e.target.value;
        this.setState({ experience })
    }

    handleBulletChange = (index, e) => {
        const experience = this.state.experience
        const bullet = experience.bullet;
        const bulletChange = bullet.map((bullet, i) => (
            (index === i) ? e.target.value : bullet
        ))
        experience.bullet = bulletChange;
        this.setState({ experience })
    }

    handleBulletDelete = (index, e) => {
        const experience = this.state.experience
        experience.bullet.splice(index, 1);
        this.setState({ experience });
    }

    handleSubmit = e => {
        e.preventDefault();
        this.setState({ experience: { bullet: [] }, showForm: false });
    }

    render() {
        const { experience, showForm, } = this.state;
        const bullets = experience.bullet;
        const { addexperience, userID } = this.props;
        const experienceAddList = (
            ExperienceList.map((list, index) =>
                (<div key={`experienceaddli+${list}+${index}`} className='form-group mb-3'>
                    <div key={`expAdddiv1+${list}+${index}`} className="input-group-prepend">
                        <span key={`experienceAddspan+${list}+${index}`} className="input-group-text" id="inputGroup-sizing-default">{list.toUpperCase()}</span>
                        <input required='required' placeholder='required' className='form-control' id="inputGroup-sizing-default" key={`experienceeditinput+${list}+${index}`} name={list} onChange={this.handleChange} value={experience[list]} />
                    </div>
                </div>))
        )
        const bulletAddList = bullets.map(
            (bullet, index) => (
                <div key={`bulletadddiv+${index}`} className="input-group mb-3">
                    <div key={`bulletadd+${index}div1`} className="input-group-prepend">
                        <span key={`bulletadd+${index}span`} className="input-group-text" id="inputGroup-sizing-default">{index + 1}</span>
                    </div>
                    <input key={`bulletinput+${index}`} placeholder='required' required='required' className="form-control" required='required' onChange={this.handleBulletChange.bind(this, index)} name={`bullet-${index}`} id="inputGroup-sizing-default" type='text' value={bullet} />
                    <button key={`bulletdelbtn+${index}`} className='fa fa-times float-right btn btn-outline-danger border-0' type='button' onClick={this.handleBulletDelete.bind(this, index)}></button>
                </div>
            ))

        return (
            <div className='container' style={{ marginTop: 100, marginBottom: 50, letterSpacing: 2 }}>
                <button className="fa fa-align-justify btn btn-secondary" type='button' style={{ marginBottom: 25 }} onClick={this.handleAdd}>Add Experience</button>
                {showForm
                    ?
                    (
                        <form className='form-group' onSubmit={this.handleSubmit}>
                            {experienceAddList}
                            <div className='form-group mb-3'>
                                <div className="input-group-prepend">
                                    <span className="input-group-text mb-3" id="inputGroup-sizing-default">BULLETS</span>
                                    <button onClick={this.handleBulletAdd} className='fa fa-plus-circle btn btn-outline-primary border-0 mb-3' type='button'></button>
                                </div>
                                <div className='form-group'>
                                    {bulletAddList}
                                </div>
                            </div>
                            <button className="btn btn-outline-primary border-0 fa fa-plus" type='submit' onClick={addexperience.bind(this, userID, experience)}></button>
                        </form>
                    )
                    : null
                }
            </div>


        )
    }
}

export default ExperienceAddForm


