import React, { Component } from 'react'
import { SchoolList } from '../components/ResumeList.js';

class SchoolItem extends Component {

    constructor(props) {
        super(props);
        this.state = { schoolUpdate: {}, edittable: false };
    }

    componentDidMount() {
        const schoolUpdate = this.props.school;
        this.setState({ schoolUpdate });
    }

    handleEdit = e => {
        this.setState({ edittable: true });
    }

    handleChange = e => {
        const schoolUpdate = this.state.schoolUpdate;
        schoolUpdate[e.target.name] = e.target.value;
        this.setState({ schoolUpdate })
    }

    handleUpdate = e => {
        e.preventDefault();
        this.setState({ edittable: false });
    }

    handleDelete = e => {
        e.preventDefault();
        this.setState({ edittable: false });
    }

    render() {
        const { deleteschool, userID, updateschool, school } = this.props;
        const { schoolUpdate, edittable } = this.state;
        const SchoolItem =
            <div className='card card-body text-center' >
                <div>
                    {
                        edittable ? null :
                            <button className='btn btn-outline-warning fa fa-edit float-left border-0' onClick={this.handleEdit}>
                            </button>
                    }
                    <button className='fa fa-times float-right btn btn-outline-danger border-0' type='submit' onClick={deleteschool}></button>
                </div>
                <div className='form-group'>
                    <form onSubmit={this.handleUpdate}>
                        {
                            SchoolList.map(
                                (list, index) =>
                                    <div key={`schoolupdatediv0${index}`}>
                                        {edittable ?
                                            <div className='input-group mb-3' key={`schoolupdatediv1${index}`}>
                                                <div className="input-group-prepend" key={`${index}schooldiv2`}>
                                                    <span className="input-group-text" id="inputGroup-sizing-default" key={`schoolspaninput${index}`}>{list.toUpperCase()}</span>
                                                </div>
                                                <input className='form-control' required='required' placeholder='required' key={`schoolinputedit${index}`} name={list} onChange={this.handleChange} value={schoolUpdate[list]} />
                                            </div>
                                            :
                                            <div className='input-group mb-3' key={`schoolupdatediv3${index}`}>
                                                <span className="input-group-text" id="inputGroup-sizing-default" key={`schoolspanli${index}`}>{list.toUpperCase()}</span>
                                                <div className='form-control' key={`${index}div4`}>{school[list]}</div>
                                            </div>

                                        }
                                    </div>
                            )
                        }
                        {!edittable
                            ? null
                            : <button className='btn btn-outline-dark border-0 fa fa-pencil' type='submit' onClick={updateschool.bind(this, userID, school._id, schoolUpdate)}></button>
                        }
                    </form>
                </div>
            </div>

        return (
            <div>
                {SchoolItem}
            </div>
        )
    }
}

export default SchoolItem;




