import React, { Component } from 'react';
import { connect } from 'react-redux';
import { fetchprofile, updateprofile, } from '../store/actions/profile.js';
import ProfileItem from '../components/ProfileItem.js';


class Profile extends Component {

    componentDidMount() {
        this.props.fetchprofile(this.props.userID);
    }

    render() {
        const {profiles,updateprofile,userID} = this.props;
        const ProfileList = 
        profiles.map(profile=>
            <ProfileItem
            key = {`profile+${profile._id}`} 
            profile = {profile}
            userID = {userID}
            updateprofile = {updateprofile}
            />
        )
        
        return (
            <div style = {{marginTop:200}}>
            {ProfileList}
            </div>
        )
    }

}

function mapStateToProps(state) {
    return {
        profiles: state.profile,
        userID: state.currentUser.user.id
    }
}

export default connect(mapStateToProps, { fetchprofile, updateprofile, })(Profile);


