import React, { Component } from 'react';
import { connect } from 'react-redux';
import { CourseworkList } from '../components/ResumeList.js';
import { addcoursework } from '../store/actions/coursework.js';

class CourseworkAddForm extends Component {
    constructor(props) {
        super(props);
        this.state = { coursework: {}, showForm: false };
    }
    handleAdd = () => {
        const showForm = !this.state.showForm
        this.setState({ showForm });
    };

    handleChange = e => {
        const coursework = this.state.coursework;
        coursework[e.target.name] = e.target.value;
        this.setState({ coursework })
    }

    handleSubmit = e => {
        e.preventDefault();
        this.setState({ coursework: {}, showForm: false });

    }

    render() {
        const { coursework, showForm } = this.state;
        const { addcoursework, userID } = this.props;
        const courseworkAddList = (
            CourseworkList.map((list, index) =>
                (
                    <div key={`courseworkaddli+${list}+${index}`} className='form-group mb-3'>
                        <div key={`courseworkAdddiv1+${list}+${index}`} className="input-group-prepend">
                            <span key={`courseworkAddspan1+${list}+${index}`} className="input-group-text" id="inputGroup-sizing-default">{list.toUpperCase()}</span>
                            <input className='form-control' type='text' required='required' placeholder='required' id="inputGroup-sizing-default" id={`coursework${list}`} key={`courseworkeditinput+${list}+${index}`} name={list} onChange={this.handleChange} value={coursework[list]} />
                        </div>
                    </div>))
        )

        return (
            <div className='container' style={{ marginTop: 100, marginBottom: 50, letterSpacing: 2 }}>
                <button className="fa fa-align-justify btn btn-secondary" type='button' style={{ marginBottom: 25 }} onClick={this.handleAdd}>Add Courseworks</button>
                {showForm
                    ?
                    (
                        <form className='form-grounp' onSubmit={this.handleSubmit}>
                            {courseworkAddList}
                            <button type='submit' className="btn btn-outline-primary fa fa-plus border-0 mb-3" onClick={addcoursework.bind(this, userID, coursework)}></button>
                        </form>
                    )
                    : null}

            </div>


        )
    }
}

export default CourseworkAddForm


