import React, { Component } from 'react';
import { connect } from 'react-redux';
import { fetchschool, addschool,updateschool, deleteschool } from '../store/actions/school.js';
import SchoolItem from '../components/SchoolItem.js';
import SchoolAddForm from '../components/SchoolAddForm.js';

class School extends Component {

    componentDidMount() {
        this.props.fetchschool(this.props.userID);
    }

    render() {
        const {schools,addschool,updateschool,deleteschool,userID} = this.props;
        const SchoolList = 
        schools.map(school=>
            <SchoolItem 
            key = {`school+${school._id}`}
            school = {school}
            userID = {userID}
            updateschool = {updateschool}
            deleteschool = {deleteschool.bind(this,userID,school._id)}
            />
        )
        
        return (
            <div>
            <SchoolAddForm 
            addschool = {addschool}
            userID = {userID}
            />
               {SchoolList}
            </div>
        )
    }

}

function mapStateToProps(state) {
    return {
        schools: state.school,
        userID: state.currentUser.user.id
    }
}

export default connect(mapStateToProps, { fetchschool, addschool,updateschool, deleteschool })(School);


