import React, { Component } from 'react';
import { connect } from 'react-redux';
import { SkillList } from '../components/ResumeList.js';
import { addskill } from '../store/actions/skill.js';

class SkillAddForm extends Component {
    constructor(props) {
        super(props);
        this.state = { skill: {}, showForm: false };
    }
    handleAdd = () => {
        const showForm = !this.state.showForm
        this.setState({ showForm });
    };

    handleChange = e => {
        const skill = this.state.skill;
        skill[e.target.name] = e.target.value;
        this.setState({ skill })
    }

    handleSubmit = e => {
        e.preventDefault();
        this.setState({ skill: {}, showForm: false });

    }

    render() {
        const { skill, showForm } = this.state;
        const { addskill, userID } = this.props;
        const skillAddList = (
            SkillList.map((list, index) =>
                (
                    <div key={`skilladdli+${list}+${index}`} className='form-group mb-3'>
                        <div key={`skillAdddiv1+${list}+${index}`} className="input-group-prepend">
                            <span key={`skillAddspan1+${list}+${index}`} className="input-group-text" id="inputGroup-sizing-default">{list.toUpperCase()}</span>
                            <input className='form-control' required='required' placeholder='required' type='text' id="inputGroup-sizing-default" id={`skill${list}`} key={`skilleditinput+${list}+${index}`} name={list} onChange={this.handleChange} value={skill[list]} />
                        </div>
                    </div>))
        )

        return (
            <div className='container' style={{ marginTop: 100, marginBottom: 50, letterSpacing: 2 }}>
                <button className="fa fa-align-justify btn btn-secondary" type='button' style={{ marginBottom: 25 }} onClick={this.handleAdd}>Add Skills</button>
                {showForm
                    ?
                    (
                        <form className='form-grounp' onSubmit={this.handleSubmit}>
                            {skillAddList}
                            <button type='submit' className="btn btn-outline-primary fa fa-plus border-0 mb-3" onClick={addskill.bind(this, userID, skill)}></button>
                        </form>
                    )
                    : null}

            </div>


        )
    }
}



export default SkillAddForm;


