import React, { Component } from 'react'
import { SkillList } from '../components/ResumeList.js';

class SkillItem extends Component {

    constructor(props) {
        super(props);
        this.state = { skillUpdate: {}, edittable: false };
    }

    componentDidMount() {
        const skillUpdate = this.props.skill;
        this.setState({ skillUpdate });
    }

    handleEdit = e => {
        this.setState({ edittable: true });
    }

    handleChange = e => {
        const skillUpdate = this.state.skillUpdate;
        skillUpdate[e.target.name] = e.target.value;
        this.setState({ skillUpdate })
    }

    handleUpdate = e => {
        e.preventDefault();
        this.setState({ edittable: false });
    }

    handleDelete = e => {
        e.preventDefault();
        this.setState({ edittable: false });
    }

    render() {
        const { deleteskill, userID, updateskill, skill } = this.props;
        const { skillUpdate, edittable } = this.state;
        const SkillItem =
            <div className='card card-body text-center' >
                <div>
                    {
                        edittable ? null :
                            <button className='btn btn-outline-warning fa fa-edit float-left border-0' onClick={this.handleEdit}>
                            </button>
                    }
                    <button className='fa fa-times float-right btn btn-outline-danger border-0' type='submit' onClick={deleteskill}></button>
                </div>
                <div className='form-group'>
                    <form onSubmit={this.handleUpdate}>
                        {
                            SkillList.map(
                                (list, index) =>
                                    <div key={`skillupdatediv1${index}`}>
                                        {edittable ?
                                            <div className='input-group mb-3' key={`skillupdatediv2${index}`}>
                                                <div className="input-group-prepend" key={`${index}skilldiv1`}>
                                                    <span className="input-group-text" id="inputGroup-sizing-default" key={`skillspaninput${index}`}>{list.toUpperCase()}</span>
                                                </div>
                                                <input className='form-control' required='required' placeholder='required' key={`skillinputedit${index}`} name={list} onChange={this.handleChange} value={skillUpdate[list]} />
                                            </div>
                                            :
                                            <div key={`skillupdatediv3${index}`}>
                                                <div key={`skillspandiv4+${index}`}>{skill[list]}</div>
                                            </div>

                                        }
                                    </div>
                            )
                        }
                        {!edittable
                            ? null
                            : <button className='btn btn-outline-dark border-0 fa fa-pencil' type='submit' onClick={updateskill.bind(this, userID, skill._id, skillUpdate)}></button>
                        }
                    </form>
                </div>
            </div>

        return (
            <div>
                {SkillItem}
            </div>
        )
    }
}

export default SkillItem;



